import React, { Component } from 'react';

import TopHeader from '../components/TopHeader'
import  Navbar from '../components/navbar'
import Footer from '../components/footer'

class withdraw extends Component {

	send = () => {
		var user = {
			name: this.refs.name.value,
			useremail: this.refs.email.value,
			mobile: this.refs.mobile.value,
			message: this.refs.message.value
		}
		var options = {
			method: 'POST',
			body: JSON.stringify(user),
			headers: {
				'Content-Type': 'application/json'
			}
		}


		fetch('/add', options)
			.then(res => res.text())
			.then(data => {
				this.refs.name.value = ''
				this.refs.email.value = ''
				this.refs.mobile.value = ''
				this.refs.message.value = ''
				alert('Success:' + data)
				
			}
				)
			.catch(error => alert('Error: Plz check your Intarnet connection!'));



	}

	render() {

		return (
			<div>
				  <TopHeader/>
        <Navbar/>          
				<div className="kode_sab_banner_wrap them_overlay">
					{/* <!--CONTAINER START--> */}
					<div className="container">
						<div className="sab_banner_text">
							<h2>Contact Us</h2>
							<ul className="breadcrumbs">
								<li><a href="contact-us.html"><i className="fa fa-home"></i></a></li>
								<li><strong>Contact Us</strong></li>
							</ul>
						</div>
					</div>
					{/* <!--CONTAINER END--> */}
				</div>




				<div className="kode_contact_des">
					<div className="container">
						<div className="row col-md-10 offset-md-1 ">
							<div className="kode_contact_field">
								<div className="section_hdg hdg_2 hdg_3">
									<a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a>
									<h3>Contact Us</h3>
									<span><i className="fa icon-building"></i></span>
								</div>
								<form method="GET" action="add" id="commentform" className="comment-form">
									<div className="col-md-4">
										<div className="kf_commet_field">
											<input placeholder="Your Name" name="author" type="text" data-default="Name*" size="30" ref="name" required="" />
										</div>
									</div>
									<div className="col-md-4">
										<div className="kf_commet_field">
											<input placeholder="Your Email" name="author" type="email" data-default="Name*" size="30" ref="email" required="" />
										</div>
									</div>
									<div className="col-md-4">
										<div className="kf_commet_field">
											<input placeholder="Your Mobile" name="author" type="number" data-default="Name*" size="30" ref="mobile" required="" />
										</div>
									</div>
									<div className="col-md-12">
										<div className="kode_textarea">
											<textarea placeholder="Your Message" name="comment" ref="message"></textarea>
										</div>
										<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={this.send} /></p>
									</div>
								</form>
							</div>
						</div>

						{/* <!--KODE CONTACT SERVICE START--> */}
						<div className="kode_contact_service">
							<ul>
								<li>
									<div className="kode_contact_text">
										<h5><a href="contact-us.html#">ADDRESS</a></h5>
										<a href="contact-us.html#"><i className="fa fa-map-marker"></i></a>
										<p><span>8569 Johanwolfgang street</span>
											Berlin Germany L, 688521
											</p>
									</div>
								</li>
								<li>
									<div className="kode_contact_text">
										<h5><a href="contact-us.html#">PHONE</a></h5>
										<a href="contact-us.html#"><i className="fa fa-map-marker"></i></a>
										<p><span>Landline : 37/5 77868 777 688</span>
											Mobile : +87 66665 7785 7
											</p>
									</div>
								</li>
								<li>
									<div className="kode_contact_text">
										<h5><a href="contact-us.html#">EMAIL ADDRESS</a></h5>
										<a href="contact-us.html#"><i className="fa fa-map-marker"></i></a>
										<p><span>General : info@islamic.com</span>
											Office : info@islamic.com
											</p>
									</div>
								</li>
							</ul>
						</div>
						{/* <!--KODE CONTACT SERVICE END--> */}
					</div>
					{/* <!--CONTAINER END--> */}
				</div>

				<Footer/>
			</div>
		)
		
	}
	
}


export default withdraw