import React, { Component } from 'react';




class withdraw extends Component {
	render() {

		var oldcode = Math.random();
		var newcode = oldcode.toString()
		var code = newcode.slice(2, 6)
		var useremail;
		var correcteamil;


		var adduser = () => {
			if (this.refs.email.value === "") {
				alert("Email Required!")
			}
			else {
				var user = {
					email: this.refs.email.value,
				}
				var options = {
					method: 'POST',
					body: JSON.stringify(user),
					headers: {
						'Content-Type': 'application/json'
					}
				}


				fetch('/email', options)
					.then(res => res.text())
					.then(data => {
						console.log(data)
						if (data==0) {
							alert("Email not existed in database...!")

						}
						else{
							var user = {
								email: this.refs.email.value,
								code: code

							}
							var options = {
								method: 'POST',
								body: JSON.stringify(user),
								headers: {
									'Content-Type': 'application/json'
								}
							}


							fetch('/forget', options)
								.then(res => res.text())
								.then(data => console.log('Success:', data))
								.catch(error => console.error('Error:', error));

							this.refs.codediv.style.display = "block"
							this.refs.emaildiv.style.display = "none"
							useremail = user.email


							
						}
					})
					.catch(error => alert('Email not existed in database!'));

			}

		}





		// check function

		var checkuser = () => {
			if (code === this.refs.code.value) {
				this.refs.codediv.style.display = "none"
				this.refs.newpass.style.display = "block"
			}
			else
				alert("code note match")

		}


		var newpassword = () => {
			if (this.refs.newpassword.value === "" || this.refs.confirmpassword.value === "") {
				alert("All Feilds Required!")
			}

			else if (this.refs.newpassword.value !== this.refs.confirmpassword.value) {
				alert("Password Not Match!")
			}
			else {
				var user = {
					newpassword: this.refs.newpassword.value,
					email: useremail,


				}
				var options = {
					method: 'POST',
					body: JSON.stringify(user),
					headers: {
						'Content-Type': 'application/json'
					}
				}


				fetch('/newpassword', options)
					.then(res => res.text())
					.then(data => {

						console.log('Success:', data)
						this.props.history.push('/login')
					})
					.catch(error => console.error('Error:', error));


			}
		}



		return (
			<div>

				<div className="kode_mosques_wrap">
					<div ref="emaildiv">

						<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>forget password</h3><span><i className="fa icon-building"></i></span></div>
						<form method="GET" action="add"  className="comment-form" className="col-md-4 offset-md-4">
							<div className="col-md-12">
								<div className="kf_commet_field">
									<input placeholder="Enter Your Email" name="author" type="email" data-default="Name*" size="30" ref="email" required="" />
								</div>
							</div>


							<div className="col-md-4 offset-md-4">

								<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={adduser} /></p>
							</div>
						</form>
					</div>

					{/* code  */}
					<div style={{ display: "none" }} ref="codediv">
						<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>validation code</h3><span><i className="fa icon-building"></i></span></div>
						<form method="GET" action="add"  className="comment-form" className="col-md-4 offset-md-4">
							<div className="col-md-12">
								<div className="kf_commet_field">
									<input placeholder="Enter Varification Code" name="author" type="" data-default="Name*" size="30" ref="code" required="" />
								</div>
							</div>


							<div className="col-md-4 offset-md-4">

								<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={checkuser} /></p>
							</div>
						</form>

					</div>
					{/* newpassword */}
					<div style={{ display: "none" }} ref="newpass">
						<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>New Password</h3><span><i className="fa icon-building"></i></span></div>

						<form method="GET" action="add"  className="comment-form" className="col-md-4 offset-md-4">
							<div className="col-md-12">
								<div className="kf_commet_field">
									<input placeholder="Enter New Password" name="author" type="password" data-default="Name*" size="30" ref="newpassword" required="" />
								</div>
							</div>
							<div className="col-md-12">
								<div className="kf_commet_field">
									<input placeholder="Confirm Password" name="author" type="password" data-default="Name*" size="30" ref="confirmpassword" required="" />
								</div>
							</div>

							<div className="col-md-4 offset-md-4">

								<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={newpassword} /></p>
							</div>
						</form>
					</div>

				</div>




			</div>


		)

	}

}


export default withdraw