          main.className = "comment_des padding";
          main.style.paddingTop = "35px";

          var figure = document.createElement("figure");
          figure.className = "them_overlay";

          var img = document.createElement("img");
          img.src =  "upload/"+this.refs.email.value+".jpg"
          // img.src = "extra-images/comment.png"

          img.style.height="100px"
          img.style.width="110px"
          figure.appendChild(img)

          main.appendChild(figure)
          var comment_text = document.createElement("div");
          comment_text.className = "comment_text";

          var comment = document.createElement("p");
          comment.innerHTML = data[i].comment
          comment_text.appendChild(comment)

          var kode_meta = document.createElement("ul");
          kode_meta.className = "kode_meta";

          var li = document.createElement("li");
          var a1 = document.createElement("a");
          a1.href = "#";



          var fa_user = document.createElement("i");
          fa_user.className = "fa fa-user";
          a1.appendChild(fa_user);
          a1.innerHTML += data[i].name;


          li.appendChild(a1);


          var li2 = document.createElement("li");
          var a2 = document.createElement("a");
          a2.href = "#";


          // var fa_share = document.createElement("i");
