import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'


class TopHeader extends Component {
	state = {
		fajar: "1",
		zuhar: "2",
		asar: "3",
		magrib: "4",
		isha: "5"
	}
	componentDidMount() {

		fetch('http://localhost:2000/prayertiming')
			.then(res => res.json())
			.then(data => {
				console.log('Success:', data)
				this.setState({
					fajar: data[0].fajar,
					zuhar: data[0].zuhar,
					asar: data[0].asar,
					magrib: data[0].magrib,
					isha: data[0].isha
				})
			})
			.catch(error => console.error('Error:', error));

	}

	logout = () => {
		fetch('/logout')
			.then(res => res.text())
			.then(data => {
				if (data = "logout") {

					console.log('Success:', data)
				}
				this.props.history.push('/login')
			})
			.catch(error => console.error('Error:', error));
	}


	render() {

		return (
			<div>
				<div className="kode_top_wrap">
					{/* <!--CONTAINER START--> */}
					<div className="container">
						{/* <!--ROW START--> */}
						<div className="row">
							<div className="col-md-6">
								{/* <!--KODE TOP INFO START--> */}
								<div className="kode_top_info">
									<ul>
										<li><a href="index.html#"><i className="fa fa-envelope"></i>contact@infomosque.com</a></li>
										<li><a href="index.html#"><i className="fa icon-technology"></i>+92 - 334 - 2797084</a></li>
										<li>
											<div className="dropdown">
												<button id="dLabel" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
													<img src="images/drop-img.png" alt="" />Eng
														<span className="fa fa-angle-down"></span>
												</button>
												<ul className="dropdown-menu" aria-labelledby="dLabel">
													<li><a href="index.html#"><img src="images/drop-img.png" alt="" />Eng</a></li>
													<li><a href="index.html#"><img src="images/drop-img.png" alt="" />urdu</a></li>
													<li><a href="index.html#"><img src="images/drop-img.png" alt="" />arabic</a></li>
												</ul>
											</div>
										</li>
									</ul>
								</div>
								{/* <!--KODE TOP INFO END--> */}
							</div>
							<div className="col-md-6">
								<div className="kode_top_right_text">
									<ul className="top_icon_fig">
										<li><a href="index.html#"><img src="images/icon-img.png" alt="" /></a></li>
										<li><a href="index.html#"><img src="images/icon-img1.png" alt="" /></a></li>
										<li><a href="index.html#"><img src="images/icon-img2.png" alt="" /></a></li>


										<li><a href="index.html#"><img src="images/icon-img3.png" alt="" /></a></li>


									</ul>

									<div className="comment-form">


										<div className="kode_mosque_item">										<div className="koed_banner_btn">
											<button className="medium_btn border btn_hover" style={{ background: "white", padding: "3px 20px" }} onClick={this.logout}>log out</button>
										</div>
										</div>
									</div>



								</div>
							</div>
						</div>
						{/* <!--ROW END--> */}
					</div>
					{/* <!--CONTAINER END--> */}
				</div>


				<div className="kode_top_logo_wrap them_overlay">
					{/* <!--CONTAINER START-->	 */}
					<div className="container">
						<div className="top_logo">
							<h1><a href="index.html#"><img src="images/top-logo.png" alt="kodeforest" /></a></h1>
						</div>
						<div className="top_time_circle">
							<ul>
								<li><a href="#">Fajr</a><span>{this.state.fajar}</span></li>
								<li><a href="#">DHUHUR</a><span>{this.state.zuhar}</span></li>
								<li><a href="#">Asar</a><span>{this.state.asar}</span></li>
								<li><a href="#">Magrib</a><span>{this.state.magrib}</span></li>
								<li><a href="#">iSHA</a><span>{this.state.isha}</span></li>
								<li style={{background:"#d2973b"}}><Link to="/time" >set timing</Link></li>
							</ul>
						</div>
					</div>
					{/* <!--CONTAINER END--> */}
				</div>

			</div>

		)

	}

}

export default withRouter(TopHeader) 