import React, { Component } from 'react';
import { Link } from 'react-router-dom'

import TopHeader from '../components/TopHeader'
import Navbar from '../components/navbar'
import Footer from '../components/footer'



class withdraw extends Component {

	adduser = () => {
		if (this.refs.password.value !== this.refs.confirm_password.value) {
			alert("password not match")
		}
		else if (this.refs.name.value === "" || this.refs.email.value === "" || this.refs.mobile.value === "" || this.refs.password.value === "" || this.refs.confirm_password.value === "", this.refs.picture.value === "" ) {
			alert("all feilds are required")
		}
		else {
			var user = {
				name: this.refs.name.value,
				email: this.refs.email.value,
				mobile: this.refs.mobile.value,
				password: this.refs.password.value,

				confirm_password: this.refs.confirm_password.value
			}
			var options = {
				method: 'POST',
				body: JSON.stringify(user),
				headers: {
					'Content-Type': 'application/json'
				}
			}

			fetch('/createaccount', options)
			.then(res => res.text())
			.then((data) =>{
				console.log('Success:', data)
				const inputFiles = document.querySelectorAll('input[type="file"]');
				const formData = new FormData();
				
				for (const file of inputFiles) {
					console.log(file.name)
					formData.append(file.name, file.files[0]);
				}
				fetch('/profile', {
					method: 'POST',
					body: formData
				})
				this.props.history.push('/login')
				})
				.catch(error => console.error('Error:', error));
				
				alert("Hy " + user.name + " Your Account Created Successfuly")
		}
	}

	render() {

		return (
			<div>
			
				<div className="kode_mosques_wrap">


					<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>Create an Account</h3><span><i className="fa icon-building"></i></span></div>

					<form method="GET" action="add" id="commentform" className="comment-form" className="col-md-4 offset-md-4">
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Your Name" name="author" type="text" data-default="Name*" size="30" ref="name" required="" />
							</div>
						</div>
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Your Email" name="author" type="email" data-default="Name*" size="30" ref="email" required="" />
							</div>
						</div>
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Your Mobile" name="author" type="number" data-default="Name*" size="30" ref="mobile" required="" />
							</div>
						</div>
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Your Password" name="author" type="password" data-default="Name*" size="30" ref="password" required="" />
							</div>
						</div>
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Confirme password" name="author" type="password" data-default="Name*" size="30" ref="confirm_password" required="" />
							</div>
						</div>
						<div className="col-md-12">
							<div className="kf_commet_field">
							{/* <input type="file" name="profilePicture" id="file" multiple /> */}
					<label><b><pre>  Select profile Picture</pre></b></label>
								<input placeholder="img" name="profilePicture" type="file" data-default="Name*" size="30" ref="picture" required="" multiple/>
							</div>
						</div>
						<div className="col-md-4 offset-md-4">

							<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={this.adduser} /></p>
						</div>
					</form>
				</div>
			</div>






		)

	}

}


export default withdraw