import React, { Component } from 'react';
import { Link } from 'react-router-dom'

import TopHeader from '../components/TopHeader'
import Navbar from '../components/navbar'
import Footer from '../components/footer'

class withdraw extends Component {

	time = () => {
		if (this.refs.fajar.value == "" || this.refs.zuhar.value == "" || this.refs.asar.value == "" || this.refs.magrib.value == "" || this.refs.isha.value == "") {
			alert("All feild are required!")
		}
		else {
			var time = {
				fajar: this.refs.fajar.value,
				zuhar: this.refs.zuhar.value,
				asar: this.refs.asar.value,
				magrib: this.refs.magrib.value,
				isha: this.refs.isha.value,
			}
			var options = {
				method: 'POST',
				body: JSON.stringify(time),
				headers: {
					'Content-Type': 'application/json'
				}
			}


			fetch('/updatatime', options)
				.then(res => res.text())
				.then(data => {
					console.log('Success:', data)
					this.props.history.push("/home")

				})
				.catch(error => console.error('Error:', error));
			console.log(time)
		}

	}


	render() {

		return (
			<div>





				<div className="kode_mosques_wrap"  >


					<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>PRAYER TIMINGS</h3><span><i className="fa icon-building"></i></span></div>

					<form id="commentform" className="comment-form" className="col-md-4 offset-md-4">
						<div className="col-md-4 offset-md-1">
							<div className="kf_commet_field">
								<h3><b>FAJAR</b></h3>
							</div>
						</div>
						<div className="col-md-6">
							<div className="kf_commet_field">
								<input type="time" id="myTime" ref="fajar" />
							</div>
						</div>
						<div className="col-md-4 offset-md-1">
							<div className="kf_commet_field">
								<h3><b>DHUHUR</b></h3>
							</div>
						</div>
						<div className="col-md-6">
							<div className="kf_commet_field">
								<input type="time" id="myTime" ref="zuhar" />
							</div>
						</div>
						<div className="col-md-4 offset-md-1">
							<div className="kf_commet_field">
								<h3><b>ASAR</b></h3>
							</div>
						</div>
						<div className="col-md-6">
							<div className="kf_commet_field">
								<input type="time" id="myTime" ref="asar" />
							</div>
						</div>
						<div className="col-md-4 offset-md-1">
							<div className="kf_commet_field">
								<h3><b>MAGRIB</b></h3>
							</div>
						</div>
						<div className="col-md-6">
							<div className="kf_commet_field">
								<input type="time" id="myTime" ref="magrib" />
							</div>
						</div>
						<div className="col-md-4 offset-md-1">
							<div className="kf_commet_field">
								<h3><b>ISHA</b></h3>
							</div>
						</div>
						<div className="col-md-6">
							<div className="kf_commet_field">
								<input type="time" id="myTime" ref="isha" />
							</div>
						</div>




						<div className=" row col-md-4 offset-md-4">

							<p className="form-submit pt-1"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Set time" onClick={this.time} /></p>
						</div>




					</form>
				</div>




			</div>


		)

	}

}


export default withdraw