import React, { Component } from 'react';
import { Link } from 'react-router-dom'

import TopHeader from '../components/TopHeader'
import Navbar from '../components/navbar'
import Footer from '../components/footer'

class withdraw extends Component {

	adduser = () => {
		var user = {
			username: this.refs.name.value,
			password: this.refs.password.value,

		}
		var options = {
			method: 'POST',
			body: JSON.stringify(user),
			headers: {
				'Content-Type': 'application/json'
			}
		}


		fetch('/aouthdatabase', options)
			.then(res => res.text())
			.then(data =>{
				console.log('Success:', data)

				var user = {
					username: this.refs.name.value,
					password: this.refs.password.value,
		
				}
				var options = {
					method: 'POST',
					body: JSON.stringify(user),
					headers: {
						'Content-Type': 'application/json'
					}
				}
		
		
				fetch('/login', options)
					.then(res => res.text())
					.then(data => {
						if (data == "good") {
							console.log(data)
							this.props.history.push('/home')
						}
						else {
							alert("Wrong username and password!")
						}
					})
					.catch(error => console.error('Error:', error));
		
				console.log(user)
		
			} )
			.catch(error => console.error('Error:', error));



	}

	render() {

		return (
			<div>

		



				<div className="kode_mosques_wrap"  >


					<div className="section_hdg hdg_2 hdg_3"><a href="contact-us.html#"><img src="images/hdg-img.png" alt="" /></a><h3>log in</h3><span><i className="fa icon-building"></i></span></div>

					<form id="commentform" className="comment-form" className="col-md-4 offset-md-4">
						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="User Name" name="username" type="text" data-default="Name*" size="30" ref="name" required="" />
							</div>
						</div>

						<div className="col-md-12">
							<div className="kf_commet_field">
								<input placeholder="Password" name="password" type="password" data-default="Name*" size="30" ref="password" required="" />
							</div>
						</div>

						<div className=" row col-md-4 offset-md-4">

							<p className="form-submit"><input name="submit" type="button" className="medium_btn background-bg-dark btn_hover hvr-wobble-bottom" value="Submit Now" onClick={this.adduser} /></p>
						</div>
						<div className="row col-md-12 " style={{ marginTop: "50px" }}>
							<div className="col-md-6 col-sm-12">

								<Link to="createaccount" style={{ fontSize: "18px", color: "#d2973b", fontWeight: "bold" }}>Create an Account</Link>

							</div>
							<div className="col-md-6 col-sm-12">
								<Link to="forget" style={{ float: "right", fontSize: "18px", color: "#d2973b", fontWeight: "bold" }}>Forget Password</Link><br />


							</div>

						</div>



					</form>
				</div>




			</div>


		)

	}

}


export default withdraw